<template>
    <div>
        <!-- 搜索页面头部 -->
        <div class="search-header">
            <!-- 后退按钮 -->
            <van-icon name="arrow-left" color="white" size="0.48rem" class="goback"
            @click="$router.back()"></van-icon>
            <!-- 搜索组件 -->
            <van-search 
            v-fofo
            v-model.trim="kw"
            placeholder="请输入搜索关键词" 
            background="#007BFF" 
            shape="round"
            @input="inputFn"
            @search="searchFn"
            ></van-search>
        </div>

        <!-- 搜索建议列表 -->  
        <div class="sugg-list" v-if="kw.length !== 0">
            <div class="sugg-item" 
            v-for="(str,index) in suggestList" :key="index"
            v-html="lightFn(str,kw)"
            @click="suggestClickFn(str)"
            >
            </div>
        </div>

        <!-- 搜索历史 -->
        <div class="search-history" v-else>
            <!-- 标题 -->
            <van-cell title="搜索历史">
                <!-- 使用 right-icon 插槽来自定义右侧图标-->
                <template #right-icon>
                    <van-icon name="delete" class="search-icon" @click="clearFn"></van-icon>
                </template>
            </van-cell>

            <!-- 历史列表 -->
            <div class="history-list">
                <span class="history-item"
                v-for="(str,index) in history" :key="index"
                @click="historyClickFn(str)"
                >{{str}}</span>
            </div>
        </div>
    </div>
</template>

<script>
import {suggestListAPI} from '@/api'
export default {
    name: 'Search',
    data () {
        return {
            kw: '',
            timer:null, // 防抖的定时器
            suggestList: [], // 联想建议列表
            history: JSON.parse(localStorage.getItem('his')) || []
        }
    },
    methods: {
        // 输入框内容改变触发的事件
        inputFn() {
            // 防抖: 延时执行逻辑代码，事件再次触发时，清除上一个定时器
            clearTimeout(this.timer)
            this.timer = setTimeout(async() => {
                if(this.kw.length === 0) return  // 防止空内容触发逻辑
                const res = await suggestListAPI({
                    keywords: this.kw
                })
                if (res.data.data.options.length === 1) {
                    this.suggestList = ''
                } else {
                    this.suggestList = res.data.data.options
                }
            }, 300)
        },

        // 专门处理字符串高亮关键字
        lightFn(originStr,target) {
            // originStr: 原来的字符串
            // target: 关键字
            // 正则的匹配条件
            const reg = new RegExp(target,'ig')
            return originStr.replace(reg, (macth) => {
                // macth就是匹配中时，原字符串里的字符
                return `<span style="color:red;">${macth}</span>`
            })
        },

        // 跳转方法-封装
        moveFn(theKw) {
            // 让路由跳转在watch之后执行
            setTimeout(() => {
                this.$router.push({
                    path: `/search_result/${theKw}`,
                })   
            })
        },

        // 输入框-搜索事件
        searchFn() {
            if (this.kw.length > 0) {
                // 搜索关键字-保存到数组中
                this.history.push(this.kw)
                this.moveFn(this.kw)
            }
        },

        // 联想菜单-点击事件
        suggestClickFn(str) {
            // 搜索关键字-保存到数组中
            this.history.push(str)
            this.moveFn(str)
        },

        // 搜索历史-点击事件
        historyClickFn(str) {
            this.moveFn(str)
        },

        // 清除历史记录
        clearFn() {
            this.history = []
        }
    },

    // watch侦听历史记录数组的改变
    watch: {
        history: {
            deep: true,
            handler() {
                // 立即覆盖式的保存到本地
                // Set: 无序不重复的value集合体 (无下角标)
                // Set类型对象 -> Array数组类型

                // const theSet = new Set(this.history)
                // let arr = Array.from(theSet)
                let arr = [...new Set(this.history)]
                  
                localStorage.setItem('his',JSON.stringify(arr))
            }
        }
    }
}    
</script>

<style scoped lang="less">
    // 搜索页面头部
    .search-header{
        height: 46px;
        display: flex;
        align-items: center;
        background-color: #007bff;
        overflow: hidden;
        .goback{
            padding-left: 14px;
        }
        .van-search{
            flex: 1;
        }
    }

    // 搜索建议列表
    .sugg-list{
        .sugg-item{
            padding:0 15px;
            border-bottom: 1px solid #f8f8f8;
            font-size: 14px;
            line-height: 50px;
            // 实现省略号的三行代码
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }

    // 搜索历史
    .sparch-icon{
        font-size:16px;
        line-height: inherit;
    }
    .history-list{
        padding: 0 10px;
        .history-item{
            display: inline-block;
            font-size: 12px;
            padding: 8px 14px;
            background-color: #efefef;
            margin: 10px 8px 0px 8px;
            border-radius: 10px;
        }
    }
</style>